import dayjs from "dayjs";
import React, { useState, useEffect, useMemo, useContext } from "react";
import { SocketContext } from "~/client/context/SocketContext";
import { LobbyEvent } from "~/shared/lobby/types";
import { Timeleft } from "./styled";
export const Countdown = ({ limit, isCurrent }) => {
    const [tick, setTick] = useState(0);
    const socket = useContext(SocketContext);
    const date = useMemo(() => dayjs().hour(0).minute(0), []);
    useEffect(() => {
        socket.on(LobbyEvent.UpdateTimeout, setTick);
        return () => {
            socket.off(LobbyEvent.UpdateTimeout, setTick);
        };
    }, []);
    return tick > 0 ? (React.createElement(Timeleft, { danger: isCurrent && tick <= Math.round(limit / 3) }, date.second(tick).format("mm:ss"))) : null;
};
